import { createContext, useEffect, useState } from "react";
import { items } from "../assets/assets";
import { toast } from "react-toastify";
import axios from "axios";

export const StoreContext = createContext(null)
const StoreContextProvider = (props)=>{
    const [loginText,setLoginText] = useState("Login/Signup")
    const [showLogin,setShowLogin] = useState(false)
    const [cartItems,setCartItems] = useState({})
    const [cartCount,setCartCount] = useState(0)
    const [totalAmount,setTotalAmount] = useState(60)
    const [singleProuductPage,setSingleProductPage] = useState('')
    const [search,setSearch] = useState('')
    const url = "https://e-commerce-backend-h9ug.onrender.com"
    const [token,setToken] = useState("")
    const toPage = (itemId)=>{
        setSingleProductPage(itemId)
        console.log(singleProuductPage)
    }
    const addToCart = async (itemId,price)=>{
        if(!cartItems[itemId]){
            setCartItems((prev)=>({...prev,[itemId]:1}))
            setCartCount(cartCount+1)
            setTotalAmount((prev)=>prev+price)
            toast.success("Item Added to Cart Successfully...")
        }
        else{
            setCartItems((prev)=>({...prev,[itemId]:prev[itemId]+1}))
            setCartCount(cartCount+1)
            setTotalAmount((prev)=>prev+price)
        }

        if(token){
       await axios.post(`${url}/api/cart/add`,{itemId},{headers:{token}})
        }
    }


    const removeFromCart = async (itemId,price) =>{
        setCartItems((prev)=>({...prev,[itemId]:prev[itemId]-1}))
        setCartCount(cartCount-1)
        setTotalAmount((prev)=>prev-price)
        if(token){
            await axios.post(`${url}/api/cart/remove`,{itemId},{headers:{token}})
             }
    }
    const removeFromCartAll = (itemId,price) =>{
        setCartItems((prev)=>({...prev,[itemId]:prev[itemId]-prev[itemId]}))
        setCartCount(cartCount-cartItems[itemId])
        setTotalAmount((prev)=>prev-(price*cartItems[itemId]))
    }
    const loadCartData = async(token)=>{
        const response = await axios.post(url+"/api/cart/get",{},{headers:{token}})
        setCartItems(response.data.catData)
    }
    useEffect(()=>{
        const loadData=async()=>{
            if(localStorage.getItem("token")){
                let response = await axios.post(url+"/api/user/login",)
                setLoginText(response.data.name)
                setToken(localStorage.getItem("token"))
                console.log("hello")
                console.log(cartItems)
            }
        }
        loadData()
    },[])

    const contextValue ={
  items,
  cartItems,
  setCartItems,
  addToCart,
  removeFromCart,
  cartCount,
  showLogin,
  setShowLogin,
  removeFromCartAll,
  totalAmount,
  toPage,
  singleProuductPage,
  search,setSearch,
  url,
  token,
  setToken,
  loginText,
  setLoginText
    }
    return(
        <StoreContext.Provider value={contextValue} >
         {props.children}
        </StoreContext.Provider>
    )
}
export default StoreContextProvider;