import React, { useContext, useState } from 'react'
import "./Contact.css"
import { StoreContext } from '../context/StoreContext'
import { toast } from 'react-toastify'
import axios from 'axios'
import { useNavigate } from 'react-router-dom'
const Contact = () => {
  const navigate  = useNavigate()
  const {url} = useContext(StoreContext)
  const [data,setData]=useState({
    fname:"",
    lname:"",
    email:"",
    phone:"",
    query:""
  })

  const onChangeHandler = (event)=>{
    const name = event.target.name;
    const value = event.target.value;
    setData(data=>({...data,[name]:value}))
  }
  const onContact = async(event) =>{
    event.preventDefault();
    let newUrl = url;
    newUrl += "/api/contact"
    const response = await axios.post(newUrl,data)
    navigate("/")
  }
  return (
    <div>
      <div>
    <div className='contactUs-container' >
      <div className="left-box">
      <div className="left-contact-container">
            <div className="first">
              <p className="head">Contact Us</p>
              <div className="content">
                <p>Email, Call or Complete the form to learn how MARKETLOOM can solve your messaging problem.</p>
              </div>
              <div className="email">
                <a href="mailto:bansall619@gmail.com">bansall619@gmail.com</a>
              </div>
              <div className="phone">
               <a href="tel:+919664114023">+91 9664114023</a>
              </div>
            </div>

            <div className="second">
              <div className="custs">
              <p className="head">Customer Support</p>
              <p className="content">
                Our Support team is available around the clock to address any concerns or enquiries you may have.
              </p>
              </div>
              <div className="feedback"> 
               <p className="head">Feedback and Suggestions</p>
               <p className="content">
                We value your feedback and are countinuously working to improve MARKETLLOM. Your input is crucial in shapping the future of MARKETLOOM.
               </p>
              </div>
              <div className="media">
                <p className="head">Media Enquiries</p>
                <p className="content">
                  Form media related questions or press inqueries, please contact us at bansall619@gmail.com .
                </p>
              </div>
            </div>
        </div>
      </div>
      <div className="contact-box">
      <div className="contact-container">
        <p className="head">
          Get in Touch
        </p>
        <p className="sub-head">
          You can reach us anytime
        </p>
        <div className="inputs">
          <div className="name">
            <input name='fname' onChange={onChangeHandler} value={data.fname} type="text" placeholder='First Name' />
            <input name='lname' onChange={onChangeHandler} value={data.lname} type="text" placeholder='Last Name' />
          </div>
          <input name='email' onChange={onChangeHandler} value={data.email} type="email"  placeholder='Email' id="" />
          <input  name='phone' onChange={onChangeHandler} value={data.phone} type="number" placeholder='Phone Number' id="" />
          <textarea  name='query' onChange={onChangeHandler} value={data.query} id="" placeholder='How Can I Help You?' ></textarea>
          <div className="contact-btn">
            <button onClick={onContact} >Submit</button>
          </div>
          <div className="term">
          <p>By Contacting Us, you agree to our terms of service and Privacy Policy. </p>
          </div>
        </div>
      </div>
      </div>
    </div>
    </div>
    <div className="bottom-container">
      <div className="map-conainer">
      <iframe className='iframe' src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d14240.47739825133!2d75.82508500000002!3d26.836156000000003!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x396db61bb7532e7f%3A0x22ff59c4a2ae1a51!2s306%2C%20Prem%20Nagar%20Rd%2C%20Prem%20Nagar%2C%20Nandpuri%20Colony%2C%20Malviya%20Nagar%2C%20Jaipur%2C%20Rajasthan%20302017!5e0!3m2!1sen!2sin!4v1720329456274!5m2!1sen!2sin" width="600" height="450" allowfullscreen={true} loading="lazy" referrerpolicy="no-referrer-when-downgrade"></iframe>
      </div>
      <div className="info">
        <div className="head">
          <p className="sub-head">Our Location</p>
          <p className="main-head">Connecting Near and Far</p>
        </div>
        <div className="head-quarters">
          <p className="head-quart">Head Quarters</p>
          <p className="firs">MARKETLOOM line</p>
          <p className="secon">306, Prem Nagar</p>
          <p className="third">Jagatpura, Jaipur</p>
          <p className="fourth">Rajasthan, India</p>
        </div>
      </div>
    </div>
    </div>
  )
}

export default Contact
