import React, { useContext, useState } from 'react'
import "./Sign.css"
import CloseIcon from '@mui/icons-material/Close';
import { StoreContext } from '../context/StoreContext';
import { Button, emphasize } from '@mui/material';
import axios from "axios"
import { toast } from 'react-toastify';
const Sign = () => {

  const {setShowLogin,url,setToken,setLoginText} = useContext(StoreContext)
  const [signLogin,setSignLogin] = useState("login")

  const [data,setData] = useState({
    name:"",
    email:"",
    password:""
  })
  const onChangeHandler = (event)=>{
    const name = event.target.name;
    const value = event.target.value;
    setData(data=>({...data,[name]:value}))
  }
  const onLogin =  async(event)=>{
     event.preventDefault()
     let newUrl = url;
     if(signLogin==="login"){
      newUrl += "/api/user/login"
     }
     else{
      newUrl += "/api/user/register"
     }
     const response = await axios.post(newUrl,data)
     if(response.data.success){
      setToken(response.data.token)
      localStorage.setItem("token",response.data.token)
      setLoginText(response.data.name)
       setShowLogin(false)
      
      if(signLogin==="login"){
        toast.success("Login Successfully")
      }
      if(signLogin==="sign")
      {
        toast.success("Account Successfully Created.")
      }
     }
     else{
     toast.error(response.data.message)
     }
  }
  return (
    <div className='signContainer'>
      <div className="sign-pop">
        <div style={{textAlign:'end',justifyContent:'space-between',display:'flex'}} className="close">
        {signLogin==="sign"?  <p className='sign-head' >SignUp</p>:  <p className='sign-head' >Login</p>}
        <CloseIcon onClick={()=>setShowLogin(false)} style={{color:'black',cursor:'pointer'}} />
        </div>
    
     <div className="sign-input-container">
      <form onSubmit={onLogin} action="">
      {signLogin==="sign"?<input name='name' onChange={onChangeHandler} value={data.name} type="text" placeholder='Enter Your Name... ' />:null}
   
      <input  name='email' onChange={onChangeHandler} value={data.email}   type="email" placeholder='Enter Your Email... ' />
      <input  name='password' onChange={onChangeHandler} value={data.password}  type="password" placeholder='Enter Your Password... ' />
      </form>
      <div className="sign-btn" style={{textAlign:'center',marginLeft:'1px',marginTop:'5px'}} >
        {signLogin==="login"?<Button onClick={onLogin} type='submit' style={{backgroundColor:"black",borderRadius:'8px',padding:'7px 150px',fontSize:'17px',textAlign:'center'}} variant='contained' >Login</Button>:<Button onClick={onLogin} type='submit' style={{backgroundColor:"black",borderRadius:'8px',padding:'7px 140px',fontSize:'17px',textAlign:'center'}} variant='contained' >Sign Up</Button>}
      
      </div>
     </div >
     {signLogin==="sign"?<span className='sign-login' style={{cursor:'pointer',color:"black",fontSize:'18px'}} onClick={()=>setSignLogin("login")} >Already Have an Account? Login</span>:<span className='sign-login' style={{cursor:'pointer',color:'black',fontSize:'18px'}} onClick={()=>setSignLogin("sign")} >I don't Have Account? Register</span>}
      </div>
    </div>
  )
}

export default Sign
