import React, { useContext, useEffect } from 'react'
import { StoreContext } from '../context/StoreContext'
import "./Cart.css"
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import DeleteIcon from '@mui/icons-material/Delete';
import Button from '@mui/material/Button';
import { assets } from '../assets/assets';
import { ButtonBase } from '@mui/material';
import { useNavigate } from 'react-router-dom';
const Cart = () => {
    const {cartItems,items,addToCart,removeFromCart,removeFromCartAll,totalAmount,setTotalAmount,cartCount} = useContext(StoreContext)
    useEffect(()=>{
  console.log(totalAmount)
    })
    const navigate = useNavigate()
    const placeOrder = ()=>{
      navigate("/orderpage")
    }
  return (
 <div>
  {cartCount===0?<div className='empty-cart' >
   <div className='cart-box' >
    <div className="empty-cart-img">
  <img src={assets.emptyCart} alt="" />
  <p>Your Cart Is Empty</p>
  <span> Add Items to it now </span>
  <br />
  <Button variant="contained" href="/" style={{marginTop:'18px',padding:'6px 30px',fontSize:'18px'}} >
        Shop Now
      </Button>
  </div>
  
   </div>

  </div>:<div className='cart-container' >
      <div className="cart-products">
        <p className='cart-heading' >Shopping Cart</p>
    
       {items.map((item)=>{
        if(cartItems[item.id]){
            return (
              <div key={item.id} >
                     <hr />
                   
                <div className='cart-product-container'  >
                   <img src={item.image} width='125px' height='125px' alt="" />
                   <div className='item-info' >
                    <p className='product-name' >{item.name}</p>
                    <p className='product-price' > Price: {item.price} </p>
                    <p className='product-brand' >Brand: {item.brand} </p>
                    <p className='product-qty' >Quantity: {cartItems[item.id]} </p>
                    <p className="product-total-amount" >Product Total Amount: {cartItems[item.id]*item.price}</p>
                  
                   </div>
                </div>
                <div className='item-qty-main' >
                  <div className="set-qty" >
                <RemoveCircleIcon style={{color:'red',fontSize:'37px',cursor:'pointer'}} onClick={()=>removeFromCart(item.id,item.price)} />
          <p >
          {cartItems[item.id]}
          </p>
       
          <AddCircleIcon onClick={()=>addToCart(item.id,item.price)} style={{color:'green',fontSize:'39px',cursor:'pointer'}} />
          </div>
            <div className="remove-icon" onClick={()=>removeFromCartAll(item.id,item.price)} >
            <p>Remove Item</p>
            <DeleteIcon />
            </div>
                </div>
           
                </div>
            )
        }
       })}
     
       </div>
       <div className="price-detail">
        <p className='price-detail-heading' >PRICE DETAILS</p>
        <hr />
        <div className="price-detail-container">
          <div className="price-item-count space">
        <p>Price ({cartCount} Items)</p>
        <p>{totalAmount-60}</p>
        </div>
        <div className="delivery-charges space">
          <p>Delivery Charges</p>
          <p>30</p>
        </div>
        <div className="delivery-charges space">
          <p>Secured Packaging Fee</p>
          <p>30</p>
        </div>
        <hr />
        <div className='total-amount' >
          <p>Total Amount</p>
          <p>{totalAmount}</p>
        </div>
        <hr />

        <div className="button" style={{textAlign:'end'}} >
           <button onClick={placeOrder} style={{cursor:'pointer'}}  variant="contained">Place Order</button>
           </div>
        </div>
      
       </div>
    </div>}
 </div>
  )
}

export default Cart
