import React, { useContext, useState } from "react";
import "./NavBar.css";
import { styled, alpha } from "@mui/material/styles";
import { InputBase } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import LocalMallIcon from "@mui/icons-material/LocalMall";
import { useNavigate } from "react-router-dom";
import { StoreContext } from "../context/StoreContext";
import Sign from "./Sign";
import SwipeableTemporaryDrawer from "./MobileNav";
import AccountBoxIcon from '@mui/icons-material/AccountBox';

const Search = styled("div")(({ theme }) => ({
  position: "relative",
  borderRadius: theme.shape.borderRadius,
  backgroundColor: alpha(theme.palette.common.white, 0.15),
  "&:hover": {
    backgroundColor: alpha(theme.palette.common.white, 0.25),
  },
  marginLeft: 0,
  width: "100%",
  [theme.breakpoints.up("sm")]: {
    marginLeft: theme.spacing(1),
    width: "auto",
  },
}));

const SearchIconWrapper = styled("div")(({ theme }) => ({
  padding: theme.spacing(0, 2),
  height: "100%",
  position: "absolute",
  pointerEvents: "none",
  display: "flex",
  alignItems: "center",
  justifyContent: "center",
}));

const StyledInputBase = styled(InputBase)(({ theme }) => ({
  color: "inherit",
  width: "100%",
  "& .MuiInputBase-input": {
    padding: theme.spacing(1, 1, 1, 0),
    // vertical padding + font size from searchIcon
    paddingLeft: `calc(1em + ${theme.spacing(4)})`,
    transition: theme.transitions.create("width"),
    [theme.breakpoints.up("sm")]: {
      width: "25ch",
      "&:focus": {
        width: "25ch",
      },
    },
  },
}));

const NavBar = () => {
  const { cartCount, setShowLogin, showLogin,setSearch,loginText,token,setToken } = useContext(StoreContext);
  const [active, setActive] = useState("home");
  const navigate = useNavigate();
  const [searchTerm,setSearchTerm] = useState('')
  const navPath = (path) => {
    setActive(path);
    if (path === "home") {
      navigate("/");
    } else {
      navigate(path);
    }
  };
  
  const onLogout = ()=>{
    localStorage.removeItem("token")
    setToken("")
    navigate("/")
  }
  return (
    <div className="Nav-Bar">
      <div className="shopName" onClick={() => navPath("/")}>
        <p style={{ color: "#002984" }}>M A R K E T </p>
        <p style={{ color: "#009688", paddingLeft: "8px" }}> L O O M</p>
      </div>
      <div className="nav-item">
        <ul>
          <li
            onClick={() => navPath("home")}
            className={active === "home" ? "active" : null}
          >
            Home
          </li>

          <li
            onClick={() => navPath("about")}
            className={active === "about" ? "active" : null}
          >
            About Us
          </li>
          <li
            onClick={() => navPath("contact")}
            className={active === "contact" ? "active" : null}
          >
            Contact Us
          </li>
        </ul>
      </div>
      <div className="search">
        <Search>
          <SearchIconWrapper>
            <SearchIcon />
          </SearchIconWrapper>
          <StyledInputBase
            onKeyDown={(ev)=>{
              if(ev.key==="Enter"){
                setSearch(searchTerm)
                navPath("/search")
              }
            }}
          onChange={(val)=>setSearchTerm(val.target.value)}
            placeholder="Search for Products, Brands..."
            inputProps={{ "aria-label": "search" }}
          />
        </Search>
      </div>
      <div className="cart">
        <LocalMallIcon
          className="cart-icon"
          onClick={() => navPath("cart")}
          style={{ width: "35px", height: "60px" }}
        />
        {cartCount === 0 ? null : (
          <span className="cartCount"> {cartCount} </span>
        )}
      </div>
       {
        !token?
        <div className="sign">
<span onClick={() => setShowLogin(true)} >Login/Signup</span>
        </div>:
        <div className="navbar-profile" >
        <AccountBoxIcon style={{width:'35px',height:'40px'}} />
        <div className="nav-profile-dropdown">
            <li onClick={()=>navPath('/myorders')} >Orders</li>
            <li onClick={onLogout} >Logout</li>
        </div>
        </div>
      }

      <div className="mobilenav">
        
        <SwipeableTemporaryDrawer showLogin={showLogin} setShowLogin={setShowLogin} />
      </div>
    </div>
  );
};

export default NavBar;
