import * as React from 'react';
import { useContext, useState } from 'react'
import { items } from '../assets/assets';
import Button from '@mui/material/Button';
import "./Item.css"
import { StoreContext } from '../context/StoreContext';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import RemoveCircleIcon from '@mui/icons-material/RemoveCircle';
import { useNavigate } from 'react-router-dom';
import { NavLink } from 'react-router-dom';

const Item =({id,name,imgSrc,price,brand,images})=> {
  
  const {cartItems,addToCart,removeFromCart,toPage} = useContext(StoreContext)
  const navigate = useNavigate();
  const Page = ()=>{
    toPage(id);
    navigate(`/singleProductPage/${name}`)
  }
  return (
  <>
    <div className='card'  >
      <img  src={imgSrc} onClick={Page} alt="" />
      <div className="item-info">
        <p className="item-name" onClick={Page} >{name}</p>
        <p className="item-price">{price}</p>
        <p className="item-brand">Brand: {brand}</p>
        {cartItems[id]?<div className='cart-value'>
          <RemoveCircleIcon style={{color:'red',fontSize:'37px',cursor:'pointer'}} onClick={()=>removeFromCart(id,price)} />
          <p >
          {cartItems[id]}
          </p>
       
          <AddCircleIcon onClick={()=>addToCart(id,price)} style={{color:'green',fontSize:'39px',cursor:'pointer'}} />
        </div>:
        <Button onClick={()=>addToCart(id,price)} style={{backgroundColor:"black",borderRadius:'12px',padding:'8px 18px',fontSize:'20px',bottom:'0px'}} className='btn' variant="contained"  fullWidth >Add To Cart</Button>}
      </div>
   
    </div>
    </>
  );
}
export default Item;