import React, { useContext, useState } from "react";
import "../pages/SingleProductPage.css";
import "./Product.css"
import ReactImageMagnify from "react-image-magnify";
import { Button } from "@mui/material";
import { StoreContext } from "../context/StoreContext";
import { toast } from "react-toastify";
import { useNavigate } from "react-router-dom";

const Product = ({
  name,
  image1,
  image2,
  image3,
  image4,
  image5,
  brand,
  desc,
  price,
  id,
  banners,
  pDesc,
  category
}) => {
  const [imgSrc, setImgSrc] = useState(image1);
  const {cartItems,addToCart,items,toPage} = useContext(StoreContext);
  const add = ()=>{
    if(!cartItems[id])
        {
            addToCart(id,price);
        }
        else{
            toast.error("Ites is Already Added")
        }
  }
  const navigate = useNavigate()
  const navPath=(itemName,itemId)=>{
    toPage(itemId)
     navigate(`/singleProductPage/${itemName}`);
  }
  return (
    <>
    <div className="product-container">
      <div className="product-image-container top">
        <div className="images-container">
          <img src={image1} className={imgSrc==image1?"active":null} onMouseEnter={() => setImgSrc(image1)} alt="" />
          <img src={image2} className={imgSrc==image2?"active":null} onMouseEnter={() => setImgSrc(image2)} alt="" />
          <img src={image3} className={imgSrc==image3?"active":null} onMouseEnter={() => setImgSrc(image3)} alt="" />
          <img src={image4} className={imgSrc==image4?"active":null} onMouseEnter={() => setImgSrc(image4)} alt="" />
          <img src={image5} className={imgSrc==image5?"active":null} onMouseEnter={() => setImgSrc(image5)} alt="" />
        </div>
        <div className="main-image">
          <img src={imgSrc} alt="" className="img" />



                  <div className="cart-btn">
        <button className="addBTN" onClick={add} >Add To Cart</button>
        <button className="buyNow"  >Buy Now</button>
        </div>

        
        <div className="mobile-images-container">
          <img src={image1} className={imgSrc==image1?"active":null} onMouseEnter={() => setImgSrc(image1)} alt="" />
          <img src={image2} className={imgSrc==image2?"active":null} onMouseEnter={() => setImgSrc(image2)} alt="" />
          <img src={image3} className={imgSrc==image3?"active":null} onMouseEnter={() => setImgSrc(image3)} alt="" />
          <img src={image4} className={imgSrc==image4?"active":null} onMouseEnter={() => setImgSrc(image4)} alt="" />
          <img src={image5} className={imgSrc==image5?"active":null} onMouseEnter={() => setImgSrc(image5)} alt="" />
        </div>
        </div>

      </div>
      <div className="product-info-container">
        <p className="name">{name}</p>
        <p className="brand">Brand: {brand}</p>
        <hr />
        <p className="price"> M.R.P  {price}</p>
        <p className="fullfill">MARKETLOOM fullfilled</p>
        <p>Inclusive of all taxes</p>
        <div className="deliverycharge">
          <p>
            30 Rupees Secure Packing Charges and 30 Rupees Delivery Charges.
            We deliver Prduct within 7 Days at any Place in India.{" "}
          </p>
        </div>
        <div className="desc">
          <p className="desc-head">About This Item</p>
          <p className="des-item">{desc}</p>
        </div>
      </div>
 
    </div>
    <hr />
    <div className="product-desc">
      <h3>Product Description</h3>
      <p>{pDesc}</p>
    </div>
    <hr />
         <div className="banners">
          <h2>From The Manufacturer</h2>
          <div className="banner-img">
         <img  src={banners.banner1} alt="" />
         <img  src={banners.banner2} alt="" />
         <img  src={banners.banner3} alt="" />
         <img  src={banners.banner4} alt="" />
         <img  src={banners.banner5} alt="" />
         </div>
       </div>
       
          
       <div className="similar-products-container" >
        <p className="heading" >Similar Products</p>
        <hr />
       <div className="similar-products">
        {items.map((item)=>{
          if(item.category==category){
            return (
               <div className="card" key={item.id} onClick={()=>navPath(item.name,item.id)} >
                <img src={item.image} onClick={()=>navPath(item.name,item.id)} alt="" />
                <div className="sInfo">
                  <p className="sPrice">{item.price}</p>
                  <p className="sName" onClick={()=>navPath(item.name,item.id)} >{item.name}</p>
                  <div className="btn-container">
                  <button className="sBtn" onClick={add} >Add To Card</button>
                  </div>
                </div>
               </div>

            )
          }
        })
        }
        </div>
        <hr />
       </div>
       </>
  );
};

export default Product;
