import {React} from 'react'
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import { assets } from '../assets/assets';

const responsive = {
  superLargeDesktop: {
    // the naming can be any, depends on you.
    breakpoint: { max: 4000, min: 3000 },
    items: 1
  },
  desktop: {
    breakpoint: { max: 3000, min: 1024 },
    items: 1
  },
  tablet: {
    breakpoint: { max: 1024, min: 464 },
    items: 1
  },
  mobile: {
    breakpoint: { max: 464, min: 0 },
    items: 1
  }
};
const CarouselD = () => {
  return (
    <div style={{marginTop:'10px',marginLeft:'10px',marginRight:'10px'}} >
      <Carousel responsive={responsive}>
           <img src={assets.car1} style={{width:"100%",height:"300px",borderRadius:'5px'}}  alt="" />
           <img src={assets.car2} style={{width:"100%",height:"300px",borderRadius:'5px'}} alt="" />
           <img src={assets.car3} style={{width:"100%",height:"300px",borderRadius:'5px'}} alt="" />
           <img src={assets.car4} style={{width:"100%",height:"300px",borderRadius:'5px'}} alt="" />
           <img src={assets.car5} style={{width:"100%",height:"300px",borderRadius:'5px'}} alt="" />
</Carousel>
    </div>
  )
}

export default CarouselD
