import * as React from 'react';
import SwipeableDrawer from '@mui/material/SwipeableDrawer';
import Button from '@mui/material/Button';
import MenuIcon from '@mui/icons-material/Menu';
import { useState,useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import CloseIcon from '@mui/icons-material/Close';
import "./MobileNav.css"
import { StoreContext } from '../context/StoreContext';
export default function SwipeableTemporaryDrawer() {
  const {showLogin,setShowLogin} = React.useContext(StoreContext)
  const [state, setState] = React.useState({
    top: false,
    left: false,
    bottom: false,
    right: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event &&
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };


  const [active, setActive] = useState("home");
  const navigate = useNavigate();
  const navPath = (path)=>{
    setActive(path);
    if(path==="home"){
      navigate("/")
    }
    else{
      navigate(path)
    }
   
  }
  const {token} = useContext(StoreContext)
  return (
    <div  className="mobile-container">
        <React.Fragment >
          <Button onClick={toggleDrawer('right', true)}>
            <MenuIcon />
          </Button>
          <SwipeableDrawer
            anchor={'right'}
            open={state['right']}
            onClose={toggleDrawer('right', false)}
            onOpen={toggleDrawer('right', true)}
          >
        <div className="mobile-nav-item">
          <div className="mobile-nav-heading">
            <p>Menu</p>
            <CloseIcon style={{height:'30px',width:'30px'}} onClick={toggleDrawer('right',false)} />
          </div>
          <hr />
          <li
            onClick={() => navPath("home")}
            className={active === "home" ? "active" : null}
          >
            Home
          </li>
          <hr />

          <li
            onClick={() => navPath("about")}
            className={active === "about" ? "active" : null}
          >
            About Us
          </li>
          <hr />
          <li
            onClick={() => navPath("contact")}
            className={active === "contact" ? "active" : null}
          >
            Contact Us
          </li>
          <hr />
          <li
            onClick={() => navPath("myOrders")}
            className={active === "myOrders" ? "active" : null}
          >
          My Orders
          </li>
          <hr />
          {!token?  <div> <li onClick={()=>setShowLogin(!showLogin)} >Sign/Login</li>  <hr /> </div> :null}
       
        
      </div>
          </SwipeableDrawer>
        </React.Fragment>
    </div>
  );
}