import React, { useContext } from 'react'
import "./Category.css"
import { StoreContext } from '../context/StoreContext'
import Item from '../components/Item'
const SearchPage = () => {
const {items,search} = useContext(StoreContext)
  return (
    <div className='grocery-page' >
    <h2>Result "{search}" </h2>
    <hr />
    <div className="page-items">
      {items.map((item)=>{
          if (search == "") {
            return (
              <div>
                <Item
                  className="item"
                  id={item.id}
                  imgSrc={item.image}
                  name={item.name.slice(0, 50)}
                  price={item.price}
                  brand={item.brand}
                  images={item.images}
                />
              </div>
            );
          } else if (item.name.toLowerCase().includes(search.toLowerCase())) {
            return (
              <div>
                <Item
                  className="item"
                  id={item.id}
                  imgSrc={item.image}
                  name={item.name.slice(0, 50)}
                  price={item.price}
                  brand={item.brand}
                  images={item.images}
                />
              </div>
            );
          } else if (item.price == search) {
            return (
              <div>
                <Item
                  className="item"
                  id={item.id}
                  imgSrc={item.image}
                  name={item.name.slice(0, 50)}
                  price={item.price}
                  brand={item.brand}
                  images={item.images}
                />
              </div>
            );
          } else if (item.brand.toLowerCase().includes(search.toLowerCase())) {
            return (
              <div>
                <Item
                  className="item"
                  id={item.id}
                  imgSrc={item.image}
                  name={item.name.slice(0, 50)}
                  price={item.price}
                  brand={item.brand}
                  images={item.images}
                />
              </div>
            );
          }
          else if (item.category.toLowerCase().includes(search.toLowerCase())) {
            return (
              <div>
                <Item
                  className="item"
                  id={item.id}
                  imgSrc={item.image}
                  name={item.name.slice(0, 50)}
                  price={item.price}
                  brand={item.brand}
                  images={item.images}
                />
              </div>
            );
          }
       

        })}
    </div>
  </div>
  )
}

export default SearchPage
