    brand:"Nikon",
    color:"Black",
    description:"20.9MP DX-Format CMOS Sensor, SnapBridge Bluetooth and Wi-Fi;4K UHD Video Recording at 30 fps Multi-CAM 3500FX II 51-Point AF System Native ISO 51200, Expanded ISO 1,640,000 180k-Pixel RGB Sensor and Group Area AF; Country of Origin: Japan Compatible Mountings: Nikon F; Hardware Interface: Audio Video Port",
