import React from "react";
import "./AboutUs.css";
import { assets } from "../assets/assets";
import CloudIcon from "@mui/icons-material/Cloud";
import RemoveRedEyeIcon from "@mui/icons-material/RemoveRedEye";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import LocalFireDepartmentTwoToneIcon from '@mui/icons-material/LocalFireDepartmentTwoTone';
import AccessTimeTwoToneIcon from '@mui/icons-material/AccessTimeTwoTone';
import CloudQueueTwoToneIcon from '@mui/icons-material/CloudQueueTwoTone';
import CloudCircleTwoToneIcon from '@mui/icons-material/CloudCircleTwoTone';
const About = () => {
  return (
    <div className="about-container">
      <div className="first">
        <p className="heading">Welcome to MARKETLOOM</p>
        <p className="sub-heading">
          Explore the latest trends and enjoy seamless shopping experience at
          Trendify, your style destination.
        </p>
      </div>
      <div className="about-second">
        <div className="left">
          <p className="heading">Our Journey</p>
          <p className="sub-heading">
            From a local boutique to a leading e-commerce platform, discover the
            Trendify story.
          </p>
          <div className="trust">
            <div className="heading">
              <CloudIcon />
              <p>Trusted Quality</p>
            </div>
            <p className="sub-heading">
              We ensure top-notch quality in every product to guarantee your
              utmost satisfaction.
            </p>
          </div>

          <div className="trust">
            <div className="heading">
              <RemoveRedEyeIcon />
              <p>Exceptional Service</p>
            </div>
            <p className="sub-heading">
              Customer care is at the core of our business, aiming to provide
              help and support 24/7.
            </p>
          </div>

          <div className="trust">
            <div className="heading">
              <ShoppingCartIcon />
              <p>Fast Delivery</p>
            </div>
            <p className="sub-heading">
              Receive your orders swiftly with our efficient delivery system, at
              your convenience.
            </p>
          </div>
        </div>
        <div className="right">
          <img src={assets.aboutImage} alt="" />
        </div>
      </div>

      <div className="third-container">
        <p className="main-heading" >Why MARKETLOOM?</p>
        <p className="sub-heading" >Distinctive features that make Trendify the perfect choice for your fashion needs.</p>
        <div className="about-cards">
          <div className="enjoy">
          <LocalFireDepartmentTwoToneIcon />
          <p>We are here for you at all times. Get assistance whenever you need it with our round-the-clock support.</p>
          </div>
          <div className="secure">
                  <AccessTimeTwoToneIcon />
                  <p className="heading">Secure Payments</p>
                  <p>Your transactions are secure with our state-of-the-art encryption technology for peace of mind.</p>
          </div>
          <div className="support">
               <CloudCircleTwoToneIcon />
               <p className="heading" >24/7 Support</p>
               <p>We are here for you at all times. Get assistance whenever you need it with our round-the-clock support.</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default About;
