import React, { useContext } from 'react'
import "./Category.css"
import { StoreContext } from "../context/StoreContext";
import Item from "../components/Item"
import Paper from '@mui/material/Paper';
import {styled } from '@mui/material/styles';
const Shadow = styled(Paper)(({ theme }) => ({
  ...theme.typography.body2,
  color: theme.palette.text.secondary,
  backgroundColor:'rgba(128, 128, 128, 0.075);',
  borderRadius:'10px'
}));


const HomeAndFurniture = () => {
  const {items} = useContext(StoreContext)
  return (
    <div className='grocery-page' >
      <h2>Home And Furniture</h2>
      <hr />
      <div className="page-items">
        {items.map((item)=>{
          if(item.category=="Home And Furniture"){
            return (
              <div>
              <Item  className="item" id={item.id} imgSrc={item.image} name={item.name.slice(0,50)} price={item.price} brand={item.brand} images={item.images} />
             </div>
            )
          }
        })}
      </div>
    </div>
  )
}

export default HomeAndFurniture
