import React from 'react'
import "./Category.css"
import { assets } from '../assets/assets'
import { useNavigate } from 'react-router-dom'
const Category = () => {
  const navigate = useNavigate()
  return (
    <div className='category' >
        <div className="cat-Item" onClick={()=>navigate('/grocery')} >
        <img src={assets.grocery} alt="" />
        <p>Grocery</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/mobiles')} >
        <img src={assets.mobiles}  alt="" />
        <p>Mobiles</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/fashion')} >
        <img src={assets.fashion}  alt="" />
        <p>Fashion</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/electronics')} >
        <img src={assets.electronics}  alt="" />
        <p>Electronics</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/HomeAndFurniture')} >
        <img src={assets.homeAndFurniture}  alt="" />
        <p>Home and Furniture</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/appliances')} >
        <img src={assets.appliances}  alt="" />
        <p>Appliances</p>
        </div>
        <div className="cat-Item" onClick={()=>navigate('/BeautyProducts')} >
        <img src={assets.beautyProducts}alt="" />
        <p>Beauty Products</p>
        </div>

    </div>
  )
}

export default Category
