import CarouselD from "../components/Carousel";
import Category from "../components/Category";
import React from "react";
import "./Home.css"
import ProductList from "../components/ProductsList";
import { StoreContext } from "../context/StoreContext";
import { useContext,useState } from "react";
import { Pagination } from "@mui/material";
const Home = () => {
  const {items} = useContext(StoreContext)
  const itemPerpage = 15;
const totalPages = Math.ceil(items.length/itemPerpage);
  const [currentPage,setCurrentPage] = useState(1)
  const rows= items.slice((currentPage-1) * itemPerpage,(currentPage)*itemPerpage);
  const handlePageChange =(event,value) =>{
        console.log(event)
        console.log(value)
        setCurrentPage(value)
  }
  return (
    <div>
      <Category />
      <div className="caro">
      <CarouselD />
      </div>
      <div className="products-list">
        <ProductList rows={rows} />
        </div>
      <div className="pageNum">
      <Pagination  showFirstButton={true} showLastButton={true} size="large" className='pagination' count={totalPages} defaultPage={currentPage} onChange={(event,value)=>handlePageChange(event,value)} variant="outlined" shape="rounded" color="primary" />
      </div>
 
      </div>
    

  );
};

export default Home;
