import React, { useContext, useState } from "react";
import "./Contact.css"
import { StoreContext } from '../context/StoreContext'
import "./OrderPage.css";
import { toast } from "react-toastify";
import axios from 'axios'
const OrderPage = () => {
  const {items,cartItems,totalAmount,url,token} = useContext(StoreContext)
  const [data,setData]=useState({
    fname:"",
    lname:"",
    email:"",
    phone:"",
   streetAddress:"",
    apartment:"",
    city:"",
    state:"",
    country:"",
    pincode:"",
    deliveryInstruction:""
  })
  const onChangeHandler =(event)=>{
    const name = event.target.name
    const value = event.target.value;
    setData(data=>({...data,[name]:value}))
  }
const placeOrder = async(event)=>{
event.preventDefault();
let orderItems = []
items.map((item)=>{
  if(cartItems[item.id]>0)
  {
    let itemInfo = item;
    itemInfo["quantity"] = cartItems[item.id]
    orderItems.push(itemInfo)
  }
})
let orderdata ={
  address:data,
  items:orderItems,
  amount:totalAmount
}
let response = await axios.post(url+"/api/order/place",orderdata,{headers:{token}})

if(response.data.success){
  console.log("hello")
  const {session_url} = response.data
  window.location.replace(session_url)

}
else{
  toast.error("Error")
}

}
  return (
    <form className="delivery-information">
      <div className="delivery-text">
        <p> Delivery Information</p>
      </div>
      <div className="delivery-inputs">
        <div className="contact-info">
          <p>Contact Information </p>
          <div className="name">
            <div className="Name">
              <span>First Name</span>
              <input value={data.fname} onChange={onChangeHandler} name="fname" type="text" placeholder="Enter Your First Name..." required />
            </div>
            <div className="Name">
              <span>Last Name</span>
              <input type="text" placeholder="Enter your Last Name..." value={data.lname} onChange={onChangeHandler} name="lname" required />
            </div>
          </div>
          <span className="d-top">Email</span>
          <input type="email" placeholder="Enter  Your Email..." value={data.email} onChange={onChangeHandler} name="email" required />
          <span className="d-top">Phone Number</span>
          <input
      value={data.phone} onChange={onChangeHandler} name="phone"
            type="number"
            id=""
            placeholder="Enter Your Phone Number..."
            required
          />
        </div>
        <div className="address-info">
          <p>Address Information</p>
          <span className="d-top">Enter Street Address</span>
          <input type="text" placeholder="Enter Street Address..." value={data.streetAddress} onChange={onChangeHandler} name="streetAddress" required />
          <span className="d-top">
            {" "}
            Enter Apartment/Suite/Unit (optional)...{" "}
          </span>
          <input
         value={data.apartment} onChange={onChangeHandler} name="apartment"
            type="text"
            placeholder="Enter Apartment/Suite/Unit (optional)..."
          />
          <div className="name">
            <div className="Name">
              <span>City</span>
              <input type="text" placeholder="Enter City..." value={data.city} onChange={onChangeHandler} name="city" required />
            </div>
            <div className="Name">
              <span>State</span>
              <input type="text" placeholder="Enter State Name..." value={data.state} onChange={onChangeHandler} name="state" required />
            </div>
          </div>
          <div className="name">
            <div className="Name">
              <span>Pincode</span>
              <input
             value={data.pincode} onChange={onChangeHandler} name="pincode"
                type="number"
                id=""
                placeholder="Enter Pincode..."
                required
              />
            </div>
            <div className="Name">
              <span>Country</span>
              <input type="text" placeholder="Enter Country..." value={data.country} onChange={onChangeHandler} name="country" required />
            </div>
          </div>
        </div>
        <div className="additional-info">
          <p>Addition Information (If You Need) </p>

          <span className="d-top">Delivery Instruction (Optional) </span>
          <input type="text" placeholder="Enter Delivery Instructions..." value={data.deliveryInstruction} onChange={onChangeHandler} name="deliveryInstruction" />
               <div className="place-order-btn">
                <button type="submit" onClick={placeOrder} >Place Order</button>
               </div>
          </div>
        </div>
      </form>
  );
};

export default OrderPage;
