import React, { useContext, useState } from 'react'
import "./SingleProductPage.css"
import { StoreContext } from '../context/StoreContext'
import Product from '../components/Product'
const SingleProductPage = () => {
    const {items,singleProuductPage} = useContext(StoreContext)
    const [imgSrc,setImgSrc] = useState();
    console.log(singleProuductPage)
  return (
    <div >
        <hr />
        {items.map((item)=>{
            if(item.id===singleProuductPage){
                return(
                    <Product category={item.category} pDesc={item.productDesc} banners={item.banners} id={item.id} name={item.name} price={item.price} image1={item.images.image1} image2={item.images.image2} image3={item.images.image3} image4={item.images.image4} image5={item.images.image5} brand={item.brand} desc={item.description} />
                )
            }
        })}
    </div>
  )
}

export default SingleProductPage
